.project-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 10px;
  width: calc(33.33% - 20px);
  min-height: 350px;
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  background-color: #000;
}

.project-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #fff; /* add white color for text */
  }
  
  .pro-details p {
  margin-bottom: 10px;
  flex-grow: 1;
  color: #ddd; /* add light gray color for text */
  }
  
  .pro-btns .btn {
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 16px;
  transition: background-color 0.3s ease;
  }
  
  .pro-btns .btn:hover {
  background-color: #0069d9;
  color: #fff; /* add white color for text on hover */
  }

  
  
  
  
  

  
.project-card:nth-child(3n+1) {
  clear: left;
}

@media (max-width: 768px) {
  .project-card {
    width: calc(50% - 20px);
  }
}

@media (max-width: 576px) {
  .project-card {
    width: 100%;
  }
}

.project-card img {
  max-width: 100%;
  max-height: 200px;
  margin-bottom: 10px;
}

.project-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.pro-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.pro-details p {
  margin-bottom: 10px;
  flex-grow: 1;
}

.pro-btns {
  display: flex;
  justify-content: flex-end;
}

.pro-btns .btn {
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.pro-btns .btn:hover {
  background-color: #0069d9;
}
